.registration {
  background-image: url(../assets/movie-theater.jpg);
  min-height: 100vh;
  min-width: 100vw;
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
  padding: 0;
  border: 0;
  color: grey;
  display: flex;
  align-items: center;
  justify-content: center;
  height: 100vh;
}

.error-mesg {
  color: rgb(255, 0, 0);
}

.card.registration-card {
  background-color: rgb(13, 26, 37);
  padding: 20px;
}

.btn {
  margin-top: 20px;
}

.btn-custom {
  background-color: rgb(89, 169, 240) !important;
}