.user-view {
  background-color: #122535;
  padding: 5px;
  color: white;
  font-family: "Joan", serif;
}

.trash {
  justify-content: center !important;
}

.user-title {
  text-align: center;
}

.backbtn {
  margin-left: 40%;
  margin-right: 40%;
  padding: 5px;
}

.alignBtn {
  justify-items: center;
}

.btnStyle {
  background: none;
  color: inherit;
  border: none;
  padding: 0;
  font: inherit;
  cursor: pointer;
  outline: inherit;
}

.btnStyle:hover {
  color: #F90606;
}

.hrStyle {
  border: 4px solid #0d1a25;
}

.userNameStyle {
  color: #F90606;
}

.delete {
  font-size: xx-small;
  text-align: center;
}

.fav-card {
  background-color: #1a354b;
  color: white;
  padding-top: 10px;
}

.fav-style-card {
  background-color: #1a354b;
  color: white;
  font-family: "Joan", serif;
  text-align: center;
}