.app-container {
  margin: 0;
  padding: 0;
}

.login-container {
  background-image: url(../assets/movie-theater.jpg);
  margin: -1rem;
  min-height: 100vh;
  min-width: 100vw;
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
  padding: 0;
  border: 0;
  color: grey;
  display: flex;
  align-items: center;
  justify-content: center;
  height: 100vh;
  font-family: Helvetica, sans-serif;
}

.card.login-card {
  background-color: rgb(13, 26, 37);
  padding: 20px;
}

.btn {
  margin-top: 20px;
}

.btn-custom {
  background-color: rgb(89, 169, 240) !important;
}